import JS2CSS from "@jeff-aporta/js2css";

export default fluidCSS;

const diccionario = {};
const estructuras = {};

let permitir_actualizacion = true;

const style = document.createElement("style");
{
  style.classList.add("fluidCSS");
  document.head.appendChild(style);
}

function fluidCSS() {
  return new classFluidCSS();
}

function actualizar_style() {
  if (!permitir_actualizacion) {
    return;
  }
  // permitir_actualizacion = false;
  style.innerHTML = Object.values(estructuras).join("");
  permitir_actualizacion = true;
}

class classFluidCSS {
  constructor() {
    this.retorno = [];

    const _THIS_ = this;

    this.childs = {
      in(entity) {
        return _THIS_.#serie(` ${entity.trim()}`, "childs", props);
      },
      id(id, props) {
        return _THIS_.#serie(`#${id}`, "childsid", props);
      },
      firstLvl(entity) {
        return _THIS_.#serie(` > ${entity}`, "childsfistlvl", props);
      },
    };

    this.bros = {
      adjacent(entity) {
        return _THIS_.#serie(` + ${entity}`, "childsadjacent", props);
      },
      general(entity) {
        return _THIS_.#serie(` ~ ${entity}`, "childsgeneral", props);
      },
      checked(entity) {
        return _THIS_.#serie(`:checked ~ ${entity}`, "childsgeneral", props);
      },
    };
  }

  actualizarStyle() {
    actualizar_style();
  }

  #selectIndexRecursive(obj, index, deep = 0) {
    let r = Object.entries(obj).map(([key, val]) => {
      if (Array.isArray(val)) {
        return [key, val[index]];
      }
      if (typeof val == "object") {
        return [key, this.#selectIndexRecursive(val, index, deep + 1)];
      }
      return [key, [val][index]];
    });
    r = r.filter(([, v]) => v);
    r = r.reduce((acc, [key, obj]) => {
      acc[key] = obj;
      return acc;
    }, {});
    return r;
  }
  #props2string(props, indice_estado) {
    return JS2CSS.parseCSS(this.#selectIndexRecursive(props, indice_estado));
  }
  endArr(extra) {
    return this.end(extra).split(" ");
  }
  end(extra_clases = "") {
    if (Array.isArray(extra_clases)) {
      extra_clases = extra_clases.join(" ");
    }
    this.retorno.push(extra_clases);
    const retorno = this.retorno.join(" ").replace(/\s+/g, " ").trim();
    setTimeout(actualizar_style);
    return retorno;
  }
  #cursor(operador, name, props) {
    const struct = this.#props2string(props, 0);
    const key = [struct, operador].join("-");
    const existe = diccionario.hasOwnProperty(key);
    const mascara_cursor = Math.random()
      .toString(36)
      .replace("0.", name + "-");
    diccionario[key] ??= mascara_cursor;
    this.retorno.push(diccionario[key]);
    if (existe) {
      return this; // Ya existe la propiedad
    }

    estructuras[mascara_cursor] = `@media ${operador}{
          .${mascara_cursor} { 
            ${struct}
          }
       }`;

    return this;
  }
  attr(...args) {
    if (args.length === 2) {
      const [exp, props] = args;
      return this.#serie(`[${exp}]`, "hasattr", props);
    }
    if (args.length === 3) {
      const [attr, value, prop] = args;
      return this.#serie(`[${attr}="${value}"]`, "hasattr", {
        [prop]: value,
      });
    }
    const [attr, op, value, prop] = args;
    return this.#serie(`${attr}${op}="${value}"`, "hasattr", {
      [prop]: value,
    });
  }
  indeterminate(props) {
    return this.#serie(":indeterminate", "indeterminate", props);
  }
  checked(props) {
    return this.#serie(":checked", "checked", props);
  }
  hasChecked(props) {
    return this.#serie(":has(:checked)", "haschecked", props);
  }
  main(props) {
    return this.#serie("", "main", props);
  }
  hover(props) {
    return this.#serie(":hover", "hover", props);
  }
  isPressed(props) {
    // Alias
    return this.active(props, "isPressed");
  }
  isDisabled(props) {
    // Alias
    return this.disabled(props, "isDisabled");
  }
  disabled(props, name = "disabled") {
    return this.#serie(":disabled", name, props);
  }
  isEmpty(props) {
    // Alias
    return this.empty(props, "isEmpty");
  }
  visited(props) {
    return this.#serie(":visited", "visited", props);
  }
  unvisited(props) {
    // Alias
    return this.link(props, "unvisited");
  }
  link(props, name = "link") {
    return this.#serie(":link", name, props);
  }
  empty(props, name = "empty") {
    return this.#serie(":empty", name, props);
  }
  active(props, name = "active") {
    return this.#serie(":active", name, props);
  }
  somefocus(props) {
    // Alias
    return this.focusWithin(props, "somefocus");
  }
  focusWithin(props, name = "focuswithin") {
    return this.#serie(":focus-within", name, props);
  }
  drawfocus(props) {
    // Alias
    return this.focusVisible(props, "drawfocus");
  }
  focusVisible(props, name = "focusvisible") {
    return this.#serie(":focus-visible", name, props);
  }
  inRange(props) {
    return this.#serie(":in-range", "inrange", props);
  }
  outOfRange(props) {
    return this.#serie(":out-of-range", "outofrange", props);
  }
  whenIsvalid(props) {
    // Alias
    return this.valid(props, "whenIsValid");
  }
  valid(props, name = "valid") {
    return this.#serie(":valid", name, props);
  }
  whenIsInvalid(props) {
    // Alias
    return this.invalid(props, "whenIsInvalid");
  }
  invalid(props, name = "invalid") {
    return this.#serie(":invalid", name, props);
  }
  focus(props) {
    return this.#serie(":focus", "focus", props);
  }
  where(props) {
    return this.#serie(":where", "where", props);
  }
  firstLine(props) {
    return this.#serie("::first-line", "firstline", props);
  }
  firstLetter(props) {
    return this.#serie("::first-letter", "firstletter", props);
  }
  textSelected(props) {
    // Alias
    return this.#serie("::selection", "textselected", props);
  }
  selection(props, name = "selection") {
    return this.#serie("::selection", name, props);
  }
  before(props) {
    return this.#serie("::before", "before", props);
  }
  after(props) {
    return this.#serie("::after", "after", props);
  }
  backdrop(props, name = "backdrop") {
    return this.#serie("::backdrop", name, props);
  }
  marker(props) {
    return this.#serie("::marker", "marker", props);
  }
  isInputFile(props) {
    return this.fileSelectorButton(props, "inputFile");
  }
  fileSelectorButton(props, name = "fileSelectorButton") {
    return this.#serie("::file-selector-button", name, props);
  }
  placeholder(props) {
    return this.#serie("::placeholder", "placeholder", props);
  }
  withnoText(props) {
    return this.#serie(":placeholder-shown", "withnoText", props);
  }
  withText(props) {
    return this.#serie(":not(:placeholder-shown)", "withText", props);
  }
  whenConsultedInURL(props) {
    // Alias
    return this.target(props, "whenConsultedInURL");
  }
  target(props, name = "target") {
    return this.#serie(":target", name, props);
  }
  whenIsInFullscreen(props) {
    return this.#serie(":fullscreen", "whenIsInFullscreen", props);
  }
  isReadOnly(props) {
    return this.#serie(":read-only", "isReadOnly", props);
  }
  isEditable(props) {
    return this.#serie(":read-write", "isEditable", props);
  }
  incorrectlyLeave(props) {
    // Alias
    return this.userInvalid(props, "incorrectlyLeave");
  }
  userInvalid(props, name = "userinvalid") {
    return this.#serie(":user-invalid", name, props);
  }
  is(entity, props) {
    return this.#serie(`:is(${entity})`, "is", props);
  }
  notIs(entity, props) {
    return this.#serie(`:not(:is(${entity})`, "notis", props);
  }
  has(entity, props) {
    return this.#serie(`:has(${entity})`, "has", props);
  }
  nthChild(index, props) {
    return this.#serie(`:nth-child(${index})`, "nthchild", props);
  }
  nthLastChild(index, props) {
    return this.#serie(`:nth-last-child(${index})`, "nthlastchild", props);
  }
  nthOfType(index, props) {
    return this.#serie(`:nth-of-type(${index})`, "nthoftype", props);
  }
  nthLastOfType(index, props) {
    return this.#serie(`:nth-last-of-type(${index})`, "nthlastoftype", props);
  }
  #serie(operador, name, props) {
    const struct = this.#props2string(props, 0);
    const key = [struct, operador].join("-");
    const existe = diccionario.hasOwnProperty(key);
    const mascara_serie = Math.random()
      .toString(36)
      .replace("0.", name + "-");
    diccionario[key] ??= mascara_serie;
    this.retorno.push(diccionario[key]);
    if (existe) {
      return this; // Ya existe la propiedad
    }

    estructuras[mascara_serie] = `.${mascara_serie}${operador}{
        ${struct}
    }`;

    return this;
  }
  withTouchscreen(props) {
    return this.#cursor(
      "(hover: none) and (pointer: coarse)",
      "touchscreen",
      props
    );
  }
  inDesktop(props) {
    return this.#cursor(
      "(hover: hover) and (pointer: fine)",
      "desktop-cursor",
      props
    );
  }
  btwX(...args) {
    return this.#btw("btwX", "width", ...args);
  }
  btwY(...args) {
    return this.#btw("btwY", "height", ...args);
  }
  #btw(name, side, ...args) {
    let [limitsInside, props] = (() => {
      if (args.length == 2) {
        return args;
      }
      if (args.length == 3) {
        return [[args[0], args[1]], args[2]];
      }
    })();
    const [cota_inferior, cota_superior] = limitsInside;
    props = (() => {
      const reprop = {};
      Object.entries(props).forEach(([k, valores]) => {
        const DENTRO = valores[0];
        if (valores.length == 1) {
          valores = [DENTRO, null];
        }
        if (valores.length == 2) {
          const FUERA = valores[1];
          valores = [FUERA, DENTRO, FUERA];
        }
        if (valores.length == 3) {
          reprop[k] = valores;
        }
      });
      return reprop;
    })();
    const key = [
      name,
      cota_inferior,
      cota_superior,
      JSON.stringify(props),
    ].join("-");
    const existe = diccionario.hasOwnProperty(key);
    const mascara_btw = Math.random()
      .toString(36)
      .replace("0.", name + "-");
    diccionario[key] ??= mascara_btw;
    this.retorno.push(diccionario[key]);
    if (existe) {
      return this; // Ya existe la propiedad
    }
    estructuras[mascara_btw] = `
      ${(() => {
        const calcular_estados_clases = Object.entries(props).map(
          ([nombre_propcss, estados]) => {
            const [DEBAJO, DENTRO, ENCIMA] = estados;
            return Object.entries({
              DEBAJO,
              DENTRO,
              ENCIMA,
            })
              .map(([nombre_estado, valor_estado]) => {
                if (valor_estado) {
                  return [
                    nombre_estado,
                    `${nombre_propcss}: ${valor_estado} !important;`,
                  ];
                }
              })
              .filter(Boolean);
          }
        );
        return Object.entries({
          DEBAJO: `(${side} < ${cota_inferior}px)`,
          ENCIMA: `(${side} > ${cota_superior}px)`,
          DENTRO: `(${cota_inferior}px <= ${side}) and (${side} <= ${cota_superior}px)`,
        })
          .map(([nombre_estado_generar, regla_media_css]) => {
            const INDEX_NOMBRE_ESTADO = 0;
            const INDEX_VALOR_ESTADO = 1;
            const cuerpo = calcular_estados_clases
              .map((estados) =>
                // Buscar los estados coincidentes
                estados.find(
                  (estado) =>
                    estado[INDEX_NOMBRE_ESTADO] == nombre_estado_generar
                )
              )
              .filter(Boolean) // eliminar propiedades sin estado
              .map((estado) => estado[INDEX_VALOR_ESTADO]) //extrae los valores
              .join("");
            return `
              @media ${regla_media_css} {
                .${mascara_btw}{
                  ${cuerpo}
                }
              }
            `;
          })
          .join("\n")
          .replace(/\s+/g, " ");
      })()}
    `;

    return this;
  }
  #if({ limitBreak, op_true, op_false, side, props, name }) {
    const _THIS_ = this;
    const key = [name, limitBreak, JSON.stringify(props)].join("-");
    const existe = diccionario.hasOwnProperty(key);
    const mascara_if = Math.random()
      .toString(36)
      .replace("0.", name + "-");
    diccionario[key] ??= mascara_if;
    this.retorno.push(diccionario[key]);
    if (existe) {
      return this; // Ya existe la propiedad
    }
    diccionario[key] = mascara_if;
    estructuras[mascara_if] = [
      val({ operador: op_true, indice: 0 }),
      val({ operador: op_false, indice: 1 }),
    ]
      .join(" ")
      .replace(/\s+/g, " ")
      .trim();

    return this;

    function val({ operador, indice }) {
      return `
            @media (${side} ${operador} ${limitBreak}px) {
              .${mascara_if}{ 
                ${_THIS_.#props2string(props, indice)}
              }
            }
        `;
    }
  }
  ltX(limitBreak, props) {
    return this.#if({
      limitBreak,
      props,
      name: "ltX",
      side: "width",
      op_true: "<=",
      op_false: ">",
    });
  }
  ltY(limitBreak, props) {
    return this.#if({
      limitBreak,
      props,
      name: "ltY",
      side: "height",
      op_true: "<=",
      op_false: ">",
    });
  }
  gtX(limitBreak, props) {
    return this.#if({
      limitBreak,
      props,
      name: "gtX",
      side: "width",
      op_true: ">",
      op_false: "<=",
    });
  }
  gtY(limitBreak, props) {
    return this.#if({
      limitBreak,
      props,
      name: "gtY",
      side: "height",
      op_true: ">",
      op_false: "<",
    });
  }
  lerpX(...args) {
    const name = "lerpX";
    const unidad = "100dvw";
    return this.#lerp(name, unidad, ...args);
  }
  lerpY(...args) {
    const name = "lerpY";
    const unidad = "100dvh";
    return this.#lerp(name, unidad, ...args);
  }
  #lerp(name, unidad, ...args) {
    const abierto = "o";
    const cerrado = "e";

    let [start, end, props] = (() => {
      if (args.length == 3) {
        return args;
      }
      if (args.length == 2) {
        let [limites, props] = args;
        let [inicio, fin] = limites;
        return [inicio, fin, props];
      }
    })();
    start = Number(start);
    end = Number(end);

    props = Object.entries(props).reduce((acc, [k, v]) => {
      const kebab = k.replace(/([a-z])([A-Z])/g, "$1-$2").toLowerCase();
      acc[kebab] = v;
      return acc;
    }, {});

    props = (() => {
      const np = {};
      Object.entries(props).forEach(([k, v]) => {
        const proyecciones = ["o", "e"];
        const auto = proyecciones[1];
        v = v.map((s) => {
          if (s == "a") {
            return abierto;
          }
          if (s == "c") {
            return cerrado;
          }
          return s;
        });
        if (v.length == 2) {
          v = [auto, v[0], v[1], auto]; // es de proyección abierta
        }
        if (v.length == 3) {
          if (proyecciones.includes(v[0])) {
            v.push(auto); // se abre la proyección al final
          } else if (proyecciones.includes(v.at(-1))) {
            v.unshift(auto); // se abre la proyección al inicio
          }
        }
        if (v.length == 4) {
          np[k] = v; // Sólo se agrega si tiene 4 argumentos
        }
      });
      return np;
    })();

    const key = name + `-${start}-${end}-${JSON.stringify(props)}`;
    const existe = diccionario.hasOwnProperty(key);
    const mascara_lerp = Math.random()
      .toString(36)
      .replace("0.", name + "-");
    diccionario[key] ??= mascara_lerp;
    this.retorno.push(diccionario[key]);
    if (existe) {
      return this; // Ya existe la propiedad
    }
    diccionario[key] = mascara_lerp;
    estructuras[mascara_lerp] = `
        .${mascara_lerp} {
            ${Object.entries(props)
              .map(([k, v]) => {
                const vis = v[1] + "px";
                const vi = v[1];
                const vf = v[2];
                if (start == end) {
                  return [k, vis]; // No es posible interpolación
                }
                if (vi == vf) {
                  return [k, vis]; // No necesita interpolación
                }
                const numerador = `(${unidad} - ${start}px)`;
                const denominador = `(${end} - ${start})`;
                const t = `calc(${numerador} / ${denominador})`;
                if (v[0] == abierto && v.at(-1) == abierto) {
                  // t ∈ ℝ
                  return [k, recta_rectorial(t)];
                }
                if (v[0] == cerrado && v.at(-1) == cerrado) {
                  // 0 <= t <= 1
                  return [k, recta_rectorial(`clamp(0px, ${t}, 1px)`)];
                }
                if (v[0] == cerrado && v.at(-1) == abierto) {
                  // t >= 0
                  return [k, recta_rectorial(`max(0px, ${t})`)];
                }
                if (v[0] == abierto && v.at(-1) == cerrado) {
                  // t <= 1
                  return [k, recta_rectorial(`clamp(-999999px, ${t}, 1px)`)];
                }
                function recta_rectorial(_t_) {
                  return `calc((${vf} - ${vi}) * ${_t_} + ${vis})`;
                }
              })
              .map(([k, v]) => {
                return `${k}: ${v} !important;`;
              })
              .join("")}
        }
      `
      .replace(/\s+/g, " ")
      .trim();
    return this;
  }
}
