import { paletteLoader, registerColors } from "../manager.js";
import { scrollbarColors } from "./scrollbar.js";
import { getAllColors } from "./rules/colors.js";
import { registerThemes_PaletteGeneral } from "./palettes.js";
import { initializeThemesPolychroma } from "./palettes.polychroma.js";
export * from "./rules/colors.js";
export * from "./rules/constants.js";
export * from "./palettes.js";
export * from "./palettes.polychroma.js";
export * from "./scrollbar.js";

/**
 * Inicializa y registra todas las paletas de colores disponibles.
 * @param {object} [otherColors={}] - Colores adicionales para registrar.
 */
export function initializeThemeColors(otherColors = {}) {
  registerColors({
    ...getAllColors(),
    ...otherColors
  });
  initializeThemesPolychroma();
  Object.entries(registerThemes_PaletteGeneral).forEach(([key, value]) => {
    paletteLoader.MUIDefaultValues[key] = value;
  });
  scrollbarColors(paletteLoader);
}