import Color from "color";

/**
 * Genera los colores primarios del tema.
 * @returns {{ [key: string]: Color }} Mapeo de nombre de color a instancia Color.
 */
export function getPrimaryColors() {
  return {
    red: Color("rgb(255, 0, 0)"),
    green: Color("rgb(0, 255, 0)"),
    blue: Color("rgb(0, 0, 255)"),
    yellow: Color("rgb(255, 255, 0)"),
    magenta: Color("rgb(255, 0, 255)"),
    cyan: Color("rgb(0, 255, 255)"),
    tomato: Color("rgb(255, 99, 71)"),
    crimson: Color("rgb(220, 20, 60)"),
    skyGreen: Color("rgb(15, 146, 216)"),
    lemonGreen: Color("rgb(132, 233, 16)"),
    militaryGreen: Color("rgb(26, 128, 0)"),
    aquaBlue: Color("rgb(33, 235, 239)"),
    purple: Color("rgb(89, 0, 97)"),
    springGreen: Color("rgb(19, 231, 132)"),
    white: Color("rgb(255, 255, 255)"),
    black: Color("rgb(0, 0, 0)"),
    blackTheme: Color("rgb(40, 40, 40)"),
    gray: Color("rgb(128, 128, 128)"),
    orange: Color("rgb(255, 128, 0)"),
    pink: Color("rgb(255, 105, 180)"),
    teal: Color("rgb(0, 110, 129)"),
    navy: Color("rgb(0, 40, 126)"),
    brown: Color("rgb(139, 69, 19)"),
    violet: Color("rgb(138, 43, 226)"),
    olive: Color("rgb(128, 128, 0)"),
    amber: Color("rgb(255, 191, 0)")
  };
}
export function transformColor(color, type, key) {
  switch (type) {
    case "Light":
      return Color(`hsl(${color.hue()}, 100%, 80%)`);
    case "Accent":
      return Color(`hsl(${color.hue()}, 45%, 53%)`);
    case "pair":
      const obj = color;
      const retorno = {
        [key + "Light"]: transformColor(obj[key], "Light", key),
        [key + "Accent"]: transformColor(obj[key], "Accent", key)
      };
      return retorno;
    default:
      return color;
  }
}

/**
 * Genera los colores secundarios basados en los primarios.
 * @param {{ [key: string]: Color }} primary Colores primarios.
 * @returns {{ [key: string]: Color }} Colores secundarios.
 */
export function getSecondaryColors(primary) {
  return {
    smerald: primary.skyGreen,
    ...Object.keys(primary).reduce((acc, key) => {
      acc = {
        ...acc,
        ...transformColor(primary, "pair", key)
      };
      return acc;
    }, {})
  };
}

/**
 * Obtiene todos los colores disponibles.
 * @returns {object} Objeto con todos los colores.
 */
export function getAllColors() {
  const primaries = getPrimaryColors();
  return {
    ...primaries,
    ...getSecondaryColors(primaries)
  };
}