import JS2CSS from "@jeff-aporta/js2css";
export function customizeScrollbar({
  main,
  maindark,
  maindarker,
  back
} = {}) {
  JS2CSS.insertStyle({
    id: "scrollbar",
    objJs: {
      "*": {
        scrollbarColor: `${main} ${back}`
      },
      "::-webkit-scrollbar": {
        width: "15px",
        height: "15px"
      },
      "::-webkit-scrollbar-track": {
        background: back,
        borderRadius: "10px"
      },
      "::-webkit-scrollbar-thumb": {
        background: `linear-gradient(
          180deg,
          ${main},
          ${maindark}
        )`,
        borderRadius: "10px",
        border: `2px solid ${back}`
      },
      "::-webkit-scrollbar-thumb:hover": {
        background: `linear-gradient(
          180deg,
          ${maindark},
          ${maindarker}
        )`
      },
      "textarea, pre, code, div, .scrollbar-theme": {
        scrollbarWidth: "thin",
        scrollbarColor: `${main} ${back}`
      }
    }
  });
}